@import "~material/font/iconfont.css";
html,
body {
  width: 100%;
  height: 100%;
  background-color: #f4f5f5;
}
:export {
  baseFontSize: 75;
  elmPrimaryColor: #07c160;
  elmInfoColor: #1989fa;
  elmDangerColor: #ee0a24;
  elmWarningColor: #ff976a;
}
.single-line-overflow {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.expand {
  position: relative;
}
.expand::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin-top: -0.26666667rem;
  margin-right: -0.26666667rem;
  margin-bottom: -0.26666667rem;
  margin-left: -0.26666667rem;
}
html,
body {
  padding: 0;
  margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
p {
  margin: 0;
}
a {
  color: inherit;
  text-decoration: none;
}
img {
  object-fit: cover;
}
.clear-fix::before,
.clear-fix::after {
  content: "";
  display: block;
  visibility: hidden;
  clear: both;
}
.elm-icon {
  display: inline-block;
  line-height: 1;
}
.elm-icon-loading {
  animation: elm-rotate 2s linear infinite;
}
.elm-icon-loading circle {
  stroke: currentColor;
  stroke-width: 3;
  stroke-linecap: round;
  fill: none;
  animation: elm-circular 1.5s ease-in-out infinite;
}
@keyframes elm-rotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes elm-circular {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -40;
  }
  100% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -120;
  }
}
.elm-search {
  position: relative;
  width: 100%;
  height: 100%;
}
.elm-search-input {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding-top: 0;
  padding-right: 0.8rem;
  padding-bottom: 0;
  padding-left: 0.8rem;
  border-width: 0;
  color: #222;
  font-size: 0.37333333rem;
  outline: none;
  appearance: none;
}
.elm-search-input::-webkit-input-placeholder {
  color: #999;
}
.elm-search-icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.elm-search-icon.search {
  left: 0.21333333rem;
}
.elm-search-icon.clear {
  right: 0.21333333rem;
}
.elm-notify {
  position: fixed;
  top: 0;
  left: 0;
  transform: translateY(-100%);
  z-index: 999;
  overflow: hidden;
  width: 100%;
  height: 1.12rem;
  color: #fff;
  font-size: 0.37333333rem;
  line-height: 1.12rem;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  transition: transform 0.2s linear;
}
.elm-notify--primary {
  background-color: #1989fa;
}
.elm-notify--danger {
  background-color: #ee0a24;
}
.elm-notify--success {
  background-color: #07c160;
}
.elm-notify--warning {
  background-color: #ff976a;
}
.elm-tag {
  padding: 0.10666667rem;
  border: 1px solid #007fff;
  border-radius: 0.10666667rem;
}
.elm-loading {
  padding-top: 0.4rem;
  padding-right: 0.4rem;
  padding-bottom: 0.4rem;
  padding-left: 0.4rem;
}
.elm-loading-text {
  padding-top: 0.26666667rem;
  color: #999;
  font-size: 0.37333333rem;
}
.elm-empty {
  text-align: center;
}
.elm-empty-bg {
  width: 8rem;
  height: 8rem;
}
.elm-empty-title {
  color: #222;
  font-size: 0.37333333rem;
}
.elm-mask {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1999;
  display: flex;
  justify-content: center;
  align-items: center;
}
.elm-button {
  display: inline-block;
  border-width: 1px;
  border-style: solid;
  border-radius: 2px;
}
.elm-button-content {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.elm-button--large {
  width: 100%;
  font-size: 0.42666667rem;
  height: 1.33333333rem;
}
.elm-button--normal {
  padding-top: 0rem;
  padding-right: 0.4rem;
  padding-bottom: 0rem;
  padding-left: 0.4rem;
  font-size: 0.37333333rem;
  height: 0.85333333rem;
}
.elm-button--small {
  padding-top: 0rem;
  padding-right: 0.21333333rem;
  padding-bottom: 0rem;
  padding-left: 0.21333333rem;
  font-size: 0.32rem;
  height: 0.69333333rem;
}
.elm-button--mini {
  padding-top: 0rem;
  padding-right: 0.10666667rem;
  padding-bottom: 0rem;
  padding-left: 0.10666667rem;
  font-size: 0.32rem;
  height: 0.58666667rem;
}
.elm-button--primary {
  border-color: #07c160;
  color: #fff;
  background-color: #07c160;
}
.elm-button--info {
  border-color: #1989fa;
  color: #fff;
  background-color: #1989fa;
}
.elm-button--warning {
  border-color: #ff976a;
  color: #fff;
  background-color: #ff976a;
}
.elm-button--danger {
  border-color: #ee0a24;
  color: #fff;
  background-color: #ee0a24;
}
.elm-button--default {
  border-color: #ebedf0;
  color: #323233;
  background-color: #fff;
}
.elm-button--plain {
  background-color: #fff;
}
.elm-button--plain.elm-button--primary {
  color: #07c160;
}
.elm-button--plain.elm-button--info {
  color: #1989fa;
}
.elm-button--plain.elm-button--warning {
  color: #ff976a;
}
.elm-button--plain.elm-button--danger {
  color: #ee0a24;
}
.elm-button--disabled {
  cursor: not-allowed;
  opacity: 0.5;
}
.elm-button--round {
  border-radius: 999px;
}
.elm-image-container {
  position: relative;
  overflow: hidden;
}
.elm-image--round {
  border-radius: 50%;
}
.elm-image-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f7f8fa;
}
.elm-image-content img {
  width: 100%;
  height: 100%;
}
.elm-image-icon {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
