:export {
  baseFontSize: 75;
  elmPrimaryColor: #07c160;
  elmInfoColor: #1989fa;
  elmDangerColor: #ee0a24;
  elmWarningColor: #ff976a;
}
.single-line-overflow {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.expand {
  position: relative;
}
.expand::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin-top: -0.26666667rem;
  margin-right: -0.26666667rem;
  margin-bottom: -0.26666667rem;
  margin-left: -0.26666667rem;
}
html,
body {
  padding: 0;
  margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
p {
  margin: 0;
}
a {
  color: inherit;
  text-decoration: none;
}
img {
  object-fit: cover;
}
.clear-fix::before,
.clear-fix::after {
  content: "";
  display: block;
  visibility: hidden;
  clear: both;
}
html,
body {
  width: 100%;
  height: 100%;
  background-color: #f4f5f5;
}
